export default {
	/**
	 * 数据
	 */

	props: {
		// 是否禁用
		disabled: {
			type: Boolean,
			default: false
		},

		// placeholder
		placeholder: {
			type: String,
			default: '请选择日期时间'
		},

		// 表示有效日期时间范围的开始，字符串格式为"YYYY-MM-DD hh:mm"
		start: {
			type: String,
			default: '1970-01-01'
		},

		// 表示有效日期时间范围的结束，字符串格式为"YYYY-MM-DD hh:mm"
		end: {
			type: String,
			default: '2300-01-01'
		},

		// 有效值 year,month,day,hour,minute，表示选择器的粒度
		// fields: {
		// 	type: String,
		// 	default: 'minute'
		// },

		// 默认值
		defaultValue: {
			type: [Date, String],
			default() {
				return null;
			}
		}
	},

	/**
	 * 数据
	 */

	data() {
		return {
			range: [],
			value: [],
			dateStr: '' // 最终显示的字符串
		};
	},

	/**
	 * 组件初次加载完成
	 */
	mounted() {
		// 初始化日期数据
		this.initDateArray();

		// 设置默认值
		// this.setDefaultValue();
	},

	/**
	 * 方法
	 */
	methods: {
		/**
		 * 确认选择
		 */
		change(event) {},

		/**
		 * 设置显示的值
		 * @param {Date|String} date 日期字符串或日期对象
		 */
		setDateStr(date) {
			let dt = this.$utils.formatDate(date);
			this.dateStr = dt.YYYY + '-' + dt.MM + '-' + dt.DD + ' ' + dt.hh + ':' + dt.mm;
		},

		/**
		 * 设置range
		 */

		initDateArray() {
			console.log('初始化日期数据');
			// 有效日期
			let dateStart = new Date(this.start); // 有效日期开始
			let dateEnd = new Date(this.end); // 有效日期结束
			

			// 判断有效日期结束是否大于有效日期开始，如果不是，则将有效日期结束修改为有效日期开始往后300年
			if (dateEnd <= dateStart) {
				dateEnd = new Date(this.start);
				dateEnd.setFullYear(dateStart.getFullYear() + 300);
				dateEnd.setDate(dateEnd.getDate() - 1);
			}
			
			console.log('dateStart = ', dateStart);
			console.log('dateEnd = ', dateEnd);

			// 有效日期
			let yearStart = dateStart.getFullYear();
			let monthStart = dateStart.getMonth() + 1;
			let dayStart = dateStart.getDate();
			let hourStart = dateStart.getHours();
			let minuteStart = dateStart.getMinutes();
			let yearEnd = dateEnd.getFullYear();
			let monthEnd = dateEnd.getMonth();
			let dayEnd = dateEnd.getDate();
			let hourEnd = dateEnd.getHours();
			let minuteEnd = dateEnd.getMinutes();
			console.log('minuteStart = ' + minuteStart);
			console.log('minuteEnd = ' + minuteEnd);
			
			// 日期数组
			let dateArray = [];
			
			// 年
			for (let year = yearStart; year <= yearEnd; year++) {
				let yearItem = {
					year: year,
					text: `${year}年`,
					months: []
				};
				dateArray.push(yearItem);
			}

			// 月
			dateArray.forEach(year => {
				let monthStartIndex = year.year == yearStart ? monthStart : 1;
				let monthEndIndex = year.year == yearEnd ? monthEnd : 12;
				for (let month = monthStartIndex; month <= monthEndIndex; month++) {
					let monthItem = {
						month: month,
						text: `${month}月`,
						days: []
					};
					year.months.push(monthItem);
				}
			});

			// 日
			dateArray.forEach(year => {
				year.months.forEach(month => {
					let dayStartIndex = year.year == yearStart && month.month == monthStart ? dayStart : 1;
					let dayEndIndex = year.year == yearEnd && month.month == monthEnd ? dayEnd : new Date(year.year, month.month, 0).getDate();
					for (let day = dayStartIndex; day <= dayEndIndex; day++) {
						let dayItem = {
							day: day,
							text: `${day}日`,
							hours: []
						};
						month.days.push(dayItem);
					}
				});
			});

			// 时
			dateArray.forEach(year => {
				year.months.forEach(month => {
					month.days.forEach(day => {
						let hourStartIndex = year.year == yearStart && month.month == monthStart && day.day == dayStart ? hourStart : 0;
						let hourEndIndex = year.year == yearEnd && month.month == monthEnd && day.day == dayEnd ? hourEnd : 23;
						for (let hour = hourStartIndex; hour <= hourEndIndex; hour++) {
							let hourItem = {
								hour: hour,
								text: `${hour}时`,
								minutes: []
							};
							day.hours.push(hourItem);
						}
					});
				});
			});
			
			// 分
			dateArray.forEach(year => {
				year.months.forEach(month => {
					month.days.forEach(day => {
						day.hours.forEach(hour => {
							console.log(`${year}年${month}月${day}日${hour}时`);
							// console.log(hour);
							// console.log(year.year == yearStart && month.month == monthStart && day.day == dayStart && hour.hour == hourStart)
							// let minuteStartIndex = year.year == yearStart && month.month == monthStart && day.day == dayStart && hour.hour == hourStart ? minuteStart : 0;
							// let minuteEndIndex = year.year == yearEnd && month.month == monthEnd && day.day == dayEnd && hour.hour == hourEnd ? minuteEnd : 59;
							// console.log('minuteStartIndex = ' + minuteStartIndex + '@@@@@@@@@@' + 'minuteEndIndex = ' + minuteEndIndex)
							// for(let minute = minuteStartIndex; minute <= minuteEndIndex; minute++) {
							// 	let minuteItem = {
							// 		minute: minute,
							// 		text: `${minute}分`,
							// 		seconds: []
							// 	}
							// 	// console.log('minute', minute);
							// 	// hour.minutes.push(minuteItem);
							// }
						});
					});
				});
			});

			console.log('@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@', dateArray);
		},

		/**
		 * 设置默认值
		 */

		setDefaultValue() {
			let dateDefault = null; // 默认日期
			let dateStart = new Date(this.start); // 有效开始日期
			let dateEnd = new Date(this.end); // 有效结束日期

			// 设置默认日期
			// 如果没有设置默认日期，将默认日期设置为当前日期
			// 如果有设置默认日期，但是默认日期不在有效日期范围内，将默认日期设置为当前日期
			// 如果有设置默认日期，默认日期不在有效日期范围内，当前日期也不在有效日期范围内，设置默认日期为有效日期开始值
			if (!this.defaultValue) {
				dateDefault = new Date();
			} else {
				dateDefault = new Date(this.defaultValue);
			}
			if (dateDefault < dateStart || dateDefault > dateEnd) {
				dateDefault = new Date(this.start);
			}

			// 找到默认值的下标
			let indexYear = this.range[0].findIndex(year => {
				return parseInt(year) == dateDefault.getFullYear();
			});
			let indexMonth = this.range[1].findIndex(month => {
				return parseInt(month) == dateDefault.getMonth() + 1;
			});
			let indexDay = this.range[2].findIndex(day => {
				return parseInt(day) == dateDefault.getDate();
			});
			let indexHour = this.range[3].findIndex(hour => {
				return parseInt(hour) == dateDefault.getHours();
			});
			let indexMinute = this.range[4].findIndex(minute => {
				return parseInt(minute) == dateDefault.getMinutes();
			});
			this.value = [indexYear, indexMonth, indexDay, indexHour, indexMinute];

			// 更新 dateStr
			this.setDateStr(dateDefault);
		},

		/**
		 * 某一列的值改变时触发
		 * @param {Number} event.detail.column 表示改变了第几列（下标从0开始）
		 * @param {Number} event.detail.value 表示变更值的下标
		 */
		columnchange(event) {
			let columnIndex = event.detail.column; // 改变的列的下标
			let valueIndex = event.detail.value; // 变更值的下标

			// 改变的是 Year 这一列
			if (columnIndex == 0) {
				let dateStart = new Date(this.start); // 有效日期开始
				let yearStart = dateStart.getFullYear(); // 有效日期开始年份

				let yearAfterChange = this.range[columnIndex][valueIndex]; // 改变后的年

				// 得到 rangeMonth
				let monthStart = dateStart.getMonth() + 1; // rangeMonth 开始
				let monthEnd = yearAfterChange == yearStart ? dateEnd.getMonth() + 1 : 12; // rangeMonth 结束
				for (let month = monthStart; month <= monthEnd; month++) {
					rangeMonth.push(month + '月');
				}
			}
			// 改变的是 Month 这一列
			else if (columnIndex == 1) {
			}
			// 改变的是 Day 这一列
			else if (columnIndex == 2) {
			}
			// 改变的是 hour 这一列
			else if (columnIndex == 3) {
			}
			// 改变的是 Minute 这一列
			else if (columnIndex == 4) {
			}
		}
	}
};
